<template>
    <div>取</div>
</template>
<script>
import { mapMutations, mapGetters } from "vuex";//引入vuex
export default {
    data(){
        return {
          obj:{},//定义用于接收store的值
          string:'',
          arr:[],
        }
    },
    created(){
        this.getStore();
    },
    computed: {
        ...mapGetters(['testObj','testString','testArr'])//在computed计算属性中获取 已经存在store的值
    },
    methods:{
        getStore(){
           this.obj = this.testObj; //将store的值赋给本实例变量
           this.string = this.testString;
           this.arr = this.testArr;
           console.log('取this.obj',this.obj)
           console.log('取this.string',this.string)
           console.log('取this.arr',this.arr)
        }
    }
}
</script>




